import Vue from 'vue'
import VueRouter from 'vue-router'

//2. 安装插件包到Vue上, 
Vue.use(VueRouter);

import home from '../pages/home.vue';
import index from '../pages/index.vue';
import detail from '../pages/detail.vue';
import user from '../pages/user.vue';
import login from '../pages/login.vue';
import reg from '../pages/reg.vue';
import upload from '../pages/upload.vue';
import NoPage from '../pages/no-page.vue';

//3. 路由配置
let routes = [
  // {path: '/'}, //route  一条路由的配置
  { path: '/index', component: index }, //route  一条路由的配置
  { path: '/home', component: home }, //route  一条路由的配置
  { path: '/detail',component: detail,name:"detail"},
  { path: '/user', component: user }, //route  一条路由的配置
  { path: '/login', component: login }, //route  一条路由的配置
  { path: '/reg', component: reg }, //route  一条路由的配置
  { path: '/upload', component: upload }, //route  一条路由的配置
  { path: '/', redirect: '/index' },
  { path: '*', component: NoPage }
]


//4.路由实例
let router = new VueRouter({ //插件路由对象
  // routes:routes
  // mode:'hash',
  mode:'history',
  routes,
});


// router.go|goback|push|replace


//5.导出路由实例，让他去控制vue根
export default router