<template>
	<div class="reg">
		<Header></Header>
		<van-notice-bar
		  left-icon="volume-o"
		  text="如果您上传的头像不符合标准,我们会为您显示默认头像。"
		/>
		<div class="Rimg">
			<el-upload
			  class="avatar-uploader"
			  action="https://jsonplaceholder.typicode.com/posts/"
			  :show-file-list="false"
			  :on-success="handleAvatarSuccess"
			  :before-upload="beforeAvatarUpload">
			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
			  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>
		<van-form @submit="onSubmit">
		  <van-field
		    v-model="username"
		    name="用户名"
		    label="用户名"
		    placeholder="请输入用户名"
		    :rules="[{ required: true, message: '用户名不可为空' }]"
		  />
		  <van-field
		    v-model="password"
		    type="password"
		    name="密码"
		    label="密码"
		    placeholder="请输入密码"
		    :rules="[{ required: true, message: '密码不可为空' }]"
		  />
		  <div class="regB">
		    <!-- <van-button round block type="info" native-type="submit" id='btn'>注册</van-button> -->
			<input type="button" value="注册"  @click="rgs" id="reg"/>
		  </div>
		</van-form>
		<Footer></Footer>
		
	</div>
</template>

<script>
	import Header from '../components/header2.vue'
	import Footer from '../components/footer1.vue'
	export default{
		name:'reg',
		data(){
			return{
				   imageUrl: '',
				   username: '',
				   password: '',
			}
		},
		components:{
			Header,Footer
		},
		methods:{
			  handleAvatarSuccess(res, file) {
			        this.imageUrl = URL.createObjectURL(file.raw);
			      },
		     beforeAvatarUpload(file) {
				     const isJPG = file.type === 'image/jpeg';
				     const isLt2M = file.size / 1024 / 1024 < 2;
				 
				     if (!isJPG) {
				     this.$message.error('上传头像图片只能是 JPG 格式!');
				       }
				     if (!isLt2M) {
				     this.$message.error('上传头像图片大小不能超过 2MB!');
				        }
				      return isJPG && isLt2M;
				        },
				onSubmit(values) {
						      // console.log('submit', values);
						    },
				rgs(){
					if(this.username==""){
						alert("账号名不能为空")
					}
					if(this.password==""){
						alert("密码不能为空")
					}
					let username = this.username
					let password = this.password
					let userurl = this.imageUrl
					// console.log(username)
			
					axios(
								{
									url:"/api/rgs",
									method:"POST",
									data:{
										username:username,
										password:password,
										userurl:userurl
									}
								}
					).then((res,err)=>{
								 
								 if(res.data.status=="用户名已存在"){
									 alert("用户名已存在")
								 }else{
									 console.log(res.data)
									 this.$router.push('login')
								 }
					})
				}
			
		}
	}
	
</script>

<style >
	.van-notice-bar{margin-top: 1.5rem;}
	.reg{width: 100%;}
	 .avatar-uploader .el-upload {
	    border: 0.0625rem dashed #3A3A3A;
	    border-radius: 0.375rem;
	    cursor: pointer;
	    /* position: relative; */
	    overflow: hidden;
		width: 3rem;
		height: 3rem;
		margin-top: 2rem;margin-left: 3.5rem;margin-bottom: 4.375rem;
	  }
	  .avatar-uploader{height: 7rem;}
	  .avatar-uploader .el-upload:hover {
	    border-color: #3A3A3A;
	  }

	  .avatar-uploader-icon {
	    font-size: 1rem;
	    color: #3A3A3A;
	    width:3rem;
	    height: 3rem;
	    line-height: 3rem;
	    text-align: center;
		/* margin-top: 1rem; */
	
	  }
	  .el-icon-plus:before{
		  
		  display: block;
		  margin-top: 0rem;
	  }
	  .avatar {
	    width: 9.375rem;
	    height: 9.375rem;
	    display: block;
	  }
	  .el-upload img{width: 3rem;height: 3rem;}

	  /* #btn{background-color: aquamarine;width: 2.5rem;color: #3A3A3A;margin-left: 7.5rem;font-size: 1.25rem;margin-top: 3.125rem;} */
	  /* .el-icon-plus avatar-uploader-icon */
	  .regB #reg{width: 3rem;height: 1rem;font-size: 0.5rem;line-height: 1; box-sizing: border-box; background-color: aquamarine;margin-left: 3.5rem;border: 0.0625rem solid #DCDFE6;margin-top: 1rem;border-radius: 0.3rem;color: #606266;}
</style>
