<template>
	<div class="upload">
		<div class="upTop">
	<span @click="onClickLeft" class="backB"> < </span>
	<span @click="upload" class="goB">发布</span>
		</div>
		<div class="topic" @click="topicFn">
			<van-icon name="hot-o" />选择话题
			<span> 选择合适的话题会有更多赞哦~</span>
		</div>
		<div class="upcontent">
			<input v-model="title" type="text" class="Utitle" placeholder="标签"/>
			   <textarea v-model="message" placeholder="我的快乐源泉..." class="Utext"></textarea>
		</div>
		<div class="Ufoot">
			<div class="Uimg1" v-show="isShow" >
				<van-uploader v-model="fileList" multiple />
			</div>
			</div>
			<div class="UimgB ">
				
				
				
			
			<span class="foot-i" @click="isShow=!isShow">
				<van-icon name="user-circle-o" />
				<span>添加图片</span>
			</span>
			<span class="foot-i foot-i1">
				<van-icon name="smile-o" />
				<span>添加表情</span>
			</span>
			
			
		</div>
		
	</div>
</template>

<script>
	export default{
		name:'upload',
		data(){
			return{
				message :"",
				title:'',
				active:0,
				fileList: [
				      ],
				isShow:false,
			}
		},
		components:{
			
		},
		methods:{
			
			onClickLeft(){
				this.$router.go(-1)
			},
		
	
			upload(){
			let msg=this.message
			let title=this.title
            let username= localStorage.getItem("username")
			let imageUrl='https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4198759762,627322340&fm=26&gp=0.jpg'
			
				axios(
						{
							url:"/api/upload",
							method:"POST",
							data:{
								username:username,
								imageUrl:imageUrl,
								title:title,
								msg:msg,
							
							}
						}
			).then((res,err)=>{
						 
						 if(res.data.status=="发送失败"){
							 alert("请检查网络是否顺畅")
						 }else{
	
							 this.$router.push('home')
						 }
			}	)
			},
			topicFn(){
				this.$router.push('/topic')
			},
			UimgFn(){
			
				
			},

		}
	}
</script>

