<template>
	<div class="detail">
		<div class="detail-top">
		<span @click="onClickLeft" class="backB"> < </span>

			</div>
		<div class="Dtop">
	<span class="userimg"><img :src='item.imageUrl '/>  </span>
	<span class="username">{{item.username}}</span>
	<van-button round v-show='isShow' type="info" id='Dbtn' @click='addItem2'>关注</van-button>
	<van-button round type="info" v-show='isShow1'  id='Dbtn1' @click='removeItem'>已关注</van-button>
	<van-button round type="info"  id='Dbtn2' @click='deleteFn(item)'>删除</van-button>
		</div>
		<van-divider />
		<div class="Dcontent">
			<h2> {{item.title}}</h2>
			<p class="Dcontent">{{item.msg}}</p>
		</div>
		
	</div>
</template>

<script>
	// import {mapActions} from 'vuex'
	export default{ 
		name:'detail',
		data(){
			return{
				item:{},
				isShow:true,
				isShow1:false,
			}
		},
		mounted() {
			axios(
						{
							url:"/api/goods",
							method:"GET",
							params:{
								_id:this.$route.query._id
							}
						}
			).then((res,err)=>{
			
						this.item=res.data[0]
			})
		},
		methods:{
			// ...mapActions(['attention/addItem','removeItem']),
			onClickLeft(){
				this.$router.go(-1)
			},
			addItem2(){
				this.isShow=false
				this.isShow1=true
				let isShow=this.isShow
				let isShow1=this.isShow1
			   this.$toast('成功关注');
			   
			   let userimg=this.item.imageUrl;
			   let username=this.item.username;
			   this.$store.dispatch('attention/addItem',{userimg,username})
			},
			removeItem(){
				this.isShow=true
				this.isShow1=false
				this.$toast('取消关注');
				let userimg=this.item.imageUrl;
				let username=this.item.username;

				this.$store.dispatch('attention/removeItem',{userimg,username})

			},
			deleteFn(a){
				let msg=a.msg
				let title=a.title
				let username= a.username
				let imageUrl=a.imageUrl
					axios(
							{
								url:"/api/deleteFn",
								method:"POST",
								data:{
									username:username,
									imageUrl:imageUrl,
									title:title,
									msg:msg,
								}
							}		
				).then((res,err)=>{
							 
							 if(res.data.status=="删除失败"){
								 alert("请检查网络是否顺畅")
							 }else{
					
								 this.$router.push('home')
							 }
				}	)
			}
		},
		components:{
			
		}
	}
</script>

<style>
	.Dtop{height: 1.5rem;}
	.detail{width: 100%;}
	.userimg{display: inline-block; width: 1.5rem;height: 1.5rem;overflow: hidden;border-radius: 1.25rem;margin-left: 0.5rem;margin-top: 0.1rem;}
	.userimg img{width: 1.5rem;height: 1.5rem;}
	.username{display: inline-block;margin-left: 0.3125rem;height: 1.5rem;line-height: 1.5rem;font-size: 0.5rem;}
   	#Dbtn{background-color: cornsilk;color: royalblue; width: 2rem;height: 0.8rem;float: right;margin-top: 0.6rem;margin-right: 0.2rem;}
	.Dcontent{width: 100%;min-height:8rem;}
	.detail-top{width: 100%;height:1.5rem;background-color: #3A3A3A}
	.backB{margin-left: 0.3rem;margin-top: 0.3125rem;float: left;font-size: 0.7rem;color: #FFFFFF;}
	.Dcontent{font-size: 0.4rem;padding: 0.3125rem;box-sizing: border-box;}
    #Dbtn1{background-color: royalblue;width: 2rem;height: 0.8rem;float: right;margin-top: 0.6rem;margin-right: 0.2rem;background-color: cornsilk;color: royalblue;}
    #Dbtn2{background-color: cornsilk;float: right;width: 2rem;height: 0.8rem;margin-top: 0.6rem;margin-right: 0.2rem;border-color: red;color: red;}
</style>
