<template>
	<div class="home">
		<Header></Header>
		<van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
		 <van-swipe class="my-swipe" :autoplay="5000" indicator-color="white">
		   <van-swipe-item v-for='(item,index) of Himg' :key='index' >
		 	  <img :src="item"/>
		 	  </van-swipe-item>
		 </van-swipe>
		 <div class="case" v-for="(item,index) of Msg" @click="intoFn" :id="item._id">
		 	<div class="case-top" :id="item._id">
		 		<span class="userimg" :id="item._id"><img :src="item.imageUrl" :id="item._id" />  </span>
		 		<span class="username" :id="item._id">{{item.username}}</span>
		 	</div>
		 	<div class="case-botton" :id="item._id">
		 		<h3 class="care-title" :id="item._id">{{item.title}}</h3>
		 		<p class="case-content" :id="item._id">{{item.msg}}</p>
		 		<el-divider content-position="left" :id="item._id"></el-divider>
		 	</div>
		 
		 </div>
		</van-pull-refresh>
		
		<Footer></Footer>
	</div>
	
</template>

<script>
	import Header from '../components/header1.vue'
	import Footer from  '../components/footer2.vue'
	export default{
		name:'home',
			data(){
				return{
					isLoading: false,
					Himg:['https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1531856613,4239289058&fm=26&gp=0.jpg',
					'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2479114918,2589427650&fm=26&gp=0.jpg',
					'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3055643321,651708506&fm=26&gp=0.jpg',
					'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=4290255512,2996415885&fm=26&gp=0.jpg',
					'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1529478564,425825238&fm=26&gp=0.jpg'
					],
				Msg:""
					
				}
			},
			components:{
				Header,Footer
			},
			methods:{
		
				intoFn(){
					// console.log(event.target.id)
					this.$router.push({name:"detail",query:{_id:event.target.id}})
					
					
				},
				  onRefresh() {
				      setTimeout(() => {
				        Toast('刷新成功');
				        this.isLoading = false;
				      }, 1000);
				    },
					
			},
			mounted(){
				axios(
							{
								url:"/api/goods",
								method:"GET",
								params:{
									
								}
							}
				).then((res,err)=>{
					this.Msg=res.data
							// console.log(res)
				})
			}
			}
</script>

<style>
	.home{width: 100%;}
	.van-swipe{height: 5rem;margin-top: 1rem;}
	 .van-swipe-item {
	    color: #fff;
	    font-size: 0.8rem;
	    line-height: 9.375rem;
		width: 100%;
	  }
	  .van-swipe-item img{width: 100%;height: 4.5rem;}
	  .case{min-height:2rem;margin-bottom: 1rem;margin-left: 0.1rem;}
	  .case-top{width: 100%;height: 2rem;}
	  .userimg{display: inline-block; width: 1.5rem;height: 1.5rem;overflow: hidden;border-radius: 1rem;margin-right: 0.3125rem;margin-top: 0.1rem;}
	  .userimg img{width: 1.5rem;height: 1.5rem;}
	  .username{display: inline-block;margin-left: 0.3125rem;height: 1.5rem;line-height: 1.5rem;font-size: 0.45rem;}
	  .care-title{height: 1rem;line-height: 1rem;margin: 0; margin-left: 0.2rem; font-size: 0.6rem;}
	  .case-content{margin: 0;height: 1rem;line-height:1rem;font-size: 0.45rem;width:9.5rem; overflow: hidden;  text-overflow:ellipsis; white-space: nowrap;}
</style>
