
 <template>
   <div class="login">
     <Header>
	 </Header>
     <img :src="src" class="Clogo" />
     <el-form ref="form" :model="form" label-width="70px">
       <el-form-item label="用户名">
         <el-input v-model="form.username" placeholder="请输入内容"></el-input>
       </el-form-item>
     </el-form>
     <el-form ref="form" :model="form" label-width="70px">
       <el-form-item label="密码">
         <el-input
           v-model="form.password"
           show-password
           placeholder="请输入密码"
         >
         </el-input>
       </el-form-item>
     </el-form>
     <router-link to="/" class="forget">忘记密码？</router-link>
     <el-form ref="form" :model="form" label-width="150px">
       <!-- <el-form-item> -->
         <!-- <el-button @click="login" class="Sbtn"  style=" background-color: aquamarine;">立即登录</el-button> -->
  <div >
			<input type="button" value="登录"  @click="login" class="loginB"/>
		  </div>
       <!-- </el-form-item> -->
     </el-form>
 
     <Footer></Footer>
   </div>
 </template>
 
 <script>
 import Header from "../components/header2.vue";
 import Footer from "../components/footer1.vue";
 export default {
   name: "login",
   data() {
     return {
       src:
         "https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2347077578,341827201&fm=26&gp=0.jpg",
       username: "",
       password: "",
       msg: "",
       form: {
         username: "",
         password: "",
       },
     };
   },
   components: {
     Header,
     Footer,
   },
   methods: {
     login() {
		 let username = this.form.username
		 let password = this.form.password
		 // console.log(username)
		 axios(
			{
				url:"/api/login",
				method:"GET",
				params:{
					username:username,
					password:password
				}
			}
		 ).then((res,err)=>{
			 console.log(res.data.status.length)
			 if(res.data.status.length<1){
				 alert(res.data.status)
			 }else{
				 localStorage.setItem('username',res.data.status[0].username)
				 localStorage.setItem('userurl',res.data.status[0].userurl)
				 // alert('密码与账号不匹配')
				 this.$router.push('home')
			 }
		 })
		 
	 },
	 backFn(){
		   this.$router.go(-1)

	 }
   },
 };
 </script>
 <style >
	 .login{width: 100%;}
 .Clogo {
   width: 100%;
   height: 8rem;
   margin-top: 2rem;
 }
 .Sbtn {
   width: 2.5rem;
   height: 1rem;
   border-radius: 0.625rem;
   line-height: 1rem;
   text-align: center;
   font-size: 0.4rem;
 }
 .forget {
   font-size: 0.4rem;
   color: blue;
   margin-left: 0.8rem;
 }
